html, body {
  height: 100%; }

footer {
  padding-left: 255px;
  text-align: center;
  line-height: 2em;
  bottom: 0;
  margin-top: -2em;
  width: 100%; }

:global #root {
  min-height: 100%;
  display: flex;
  flex-direction: column; }

/** @define Header */
.Header {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  right: 0; }

/** @define Body */
.Body {
  flex: 1;
  padding-top: 50px; }
.Body-topbar {
    border: none; }
.Body-topbar :global .navbar-header {
    position: relative;
    width: 255px;
    margin-left: -15px;
    padding: 0 15px;
    background-color: #c0433e;
    text-align: center; }
.Body-topbar :global .navbar-brand {
    margin: 5px 0 !important;
    padding: 0;
    border-radius: 50%;
    height: auto;
    background: rgba(240, 240, 240, .8);
    float: none;
    display: inline-block; }
.Body-topbar :global .navbar-brand:hover {
    background: rgba(240, 240, 240, .8); }
.Body-topbar :global .g-logo {
    height: 40px;
    width: 120px;
    display: block;
    text-align: center;
    line-height: 40px; }
.Body-topbar :global .navbar {
    border: none; }
.Body-navPanel {
    position: fixed;
    top: 50px;
    bottom: 0;
    width: 255px;
    background-color: #c94540;
    overflow: auto;
    transition: left 0.5s; }
.Body-navPanel :global .sidebarMenu:first-child {
    margin: 1em 0; }
.Body-main {
    flex: 1;
    padding: 1em 1em 2em 0em;
    padding-left: calc(255px + 1em);
    line-height: 2em;
    transition: padding-left 0.5s; }
.Body-navbar-toggler {
    width: 26px;
    height: 50px;
    margin: 0;
    position: relative; }
.Body-navbar-toggler span {
    display: block;
    position: absolute;
    top: 50%;
    height: 15px;
    width: 100%;
    color: #973430; }

@media (max-width:767px) {
  .Body {}
  .Body-topbar :global .navbar-header {
      float: none;
      width: auto;
      text-align: left; }
  .Body-navPanel {
      position: fixed;
      left: -265px; }
  .Body-navPanel--in {
        left: 0; }
  .Body-main {
      padding-left: 1em; }
  .Body-main--in {
        padding-left: calc(255px + 1em); } }

@media (min-width:768px) {
  .Body {
    display: flex; }
  .Body-content {
      flex: 1; } }



